import React from "react";
import { toPng } from 'html-to-image';
import Download from 'downloadjs';

class Risko extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };
    this.handleChange = this.handleChange.bind(this);
    this.dlRegion = this.dlRegion.bind(this);
    this.captureArea = React.createRef();
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }
  handleSubmit(event) {
    alert("A name was submitted: " + this.state.value);
    event.preventDefault();
  }

  dlRegion() {
    toPng(this.captureArea.current)
    .then((dataUrl) => {
        Download(dataUrl)
    })
  }

  render() {
    return (
      <div className="w-2/5 printfull">
        <textarea
          type="text"
          className="my-2 text-black printhide"
          value={this.state.value}
          onChange={this.handleChange}
        />
        <br />
        <button className="text-4xl p-2 m-2 rounded-lg printhide bg-white text-black" onClick={this.dlRegion}>
            ♥
        </button>
        <div className="relative h-100 printarea" ref={this.captureArea}>
            <p className="absolute font-unica top-64 left-0 px-9 font-bold text-4xl text-white">{this.state.value}</p>
            <img className="printfull" src="/deadinside2.png" alt="Risko" />
        </div>
      </div>
    );
  }
}

export default Risko;
